            let burstBuf  = _this.mGetSkillMarkLine(nikke.burst, reg);
            if (nikke.skill1 != skill1Buf) { skill1 = skill1Buf; flgFind = true; };
            if (nikke.skill2 != skill2Buf) { skill2 = skill2Buf; flgFind = true; };
            if (nikke.burst  != burstBuf)  { burst  = burstBuf;  flgFind = true; };
          };
          
          if(flgFind) {
            let reg = /(<mark>)/i;
            if(!reg.test(skill1)) skill1 = "";
            if(!reg.test(skill2)) skill2 = "";
            if(!reg.test(burst)) burst = "";
            _this.skillInfos.push({ id: _this.skillInfos.length, nikkeId: nikke.id, name: skillInfoName, skill1: skill1, skill2: skill2, burst: burst });
          } else {
            disp = 'none';
          };
        };

        if (_this.coSelTab == 4) {
          if (document.getElementById("sm"+nikke.id).parentNode.id == 'smGraph') disp = ''; // グラフに配置されたニケは強制表示
          document.getElementById("sm"+nikke.id).style.display = disp;
        } else if (_this.coSelTab == 3) {
          if (document.getElementById("sf"+nikke.id).parentNode.id.startsWith('sfSquad')) disp = ''; // 部隊欄に配置されたニケは強制表示
          document.getElementById("sf"+nikke.id).style.display = disp;
        } else {
          _this.tsTalkNikkes.forEach(function(talk){
            if(talk.name == nikke.name) {
              if(talk.check) disp = true; // 面談候補上でチェック状態なら強制表示
              document.getElementById("tsNikke"+talk.id).style.display = disp;
            };
          });
        };
      })
    },

