              if(hit > nikke.hit) {
                nikke.hit = hit;
              };
              if(nikke.hit > hitMax) {
                hitMax = nikke.hit;
              };
              
              hit = _this.relevance(nikke.trigramify, cmp[1]); // 上選択肢を比較
              if(hit > nikke.hit) {
                nikke.hit = hit;
              };
              if(nikke.hit > hitMax) {
